function advertControllersInit(module) {

    module.controller('advertsFeedCtrl', ["$http", "$scope", function (ajax, $scope) {
        var _this = this;

        this.getAdverts = function (user_id) {
            if (!user_id) {
                ajax({
                    method: "get",
                    url: "/advert"
                }).then(function (response) {
                    if (response.data.adverts) {
                        _this.adverts = response.data.adverts;
                    }
                }).catch(function (err) {
                    console.log(err);
                });
            } else {
                ajax({
                    method: "get",
                    url: "/advertiser/" + user_id + "/adverts"
                }).then(function (response) {
                    console.log(response);
                    if (response.data.adverts) {
                        _this.adverts = response.data.adverts;
                    }
                }).catch(function (err) {
                    console.log(err);
                });
            }
        };

        this.getAdverts($scope.user_id);
    }]);

    module.controller('newAdvertCtrl', ["$http", function (ajax) {
        var _this2 = this;

        this.advert = {
            gender: "boy",
            type: "dog",
            age: 1
        };
        this.create = function () {
            ajax({
                method: "post",
                url: "/advert",
                data: _this2.advert
            }).then(function (response) {
                console.log(response.data);
            }).catch(function (err) {
                console.log(err);
            });
        };
    }]);

    module.controller('editAdvertCtrl', ["$http", "$scope", function (ajax, $scope) {
        var _this3 = this;

        var requestData = {};
        var current_advert = {};
        this.getAdvert = function (advert_id) {
            ajax({
                method: "get",
                url: "/advert/api/" + advert_id
            }).then(function (response) {
                if (response.data.success && response.data.advert) {
                    _this3.advert = response.data.advert;
                    if (_this3.advert.age) {
                        _this3.advert.age = parseInt(_this3.advert.age);
                    }
                    current_advert = JSON.parse(JSON.stringify(_this3.advert));
                }
            }).catch(function (err) {
                console.log(err);
            });
        };

        this.getAdvert($scope.advert_id);

        this.save = function () {
            ajax({
                method: "put",
                url: "/advert/api/" + $scope.advert_id,
                data: _this3.advert
            }).then(function (response) {
                if (response.data.success && response.data.advert) {
                    _this3.advert = response.data.advert;
                    if (_this3.advert.age) {
                        _this3.advert.age = parseInt(_this3.advert.age);
                    }
                    current_advert = JSON.parse(JSON.stringify(_this3.advert));
                }
            }).catch(function (err) {
                console.log(err);
            });
        };

        this.reset = function () {
            _this3.advert = JSON.parse(JSON.stringify(current_advert));
        };
    }]);
}

"use strict";
function authControllersInit(module) {

    module.controller('authCtrl', ["$scope", "$http", "$timeout", "authService", function ($scope, $http, $timeout, authService) {
        var _this4 = this;

        this.emailRegex = /^[-a-z0-9~!$%^&*_=+}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$/i;
        this.passwordRegex = /.*\S.*/;

        this.checkForm = function (form) {
            _this4.error = !$scope[form].$valid;
            _this4.validClass = $scope[form].$valid ? "valid" : "error";
        };

        this.submit = function (form) {
            $scope[form].email.$setTouched();
            $scope[form].password.$setTouched();

            var url = form === "registration" ? "/advertiser" : "/advertiser/login";

            if ($scope[form].$valid) {

                authService.authenticate(url, { email: _this4.email, password: _this4.password }).then(function (response) {
                    if (response.data.success) {
                        $timeout(function () {
                            _this4.responseClass = "success";
                            document.location.reload();
                        }, 3000);
                    } else {
                        console.log($scope[form] + ": failed");
                        console.log(response);
                    }
                }).catch(function (err) {
                    if (!err.data.success) {
                        $timeout(function () {
                            _this4.responseClass = "fail";
                        }, 3000);
                        console.log(err.data.message);
                    } else {
                        console.log(err);
                    }
                });
            } else {
                console.log($scope[form] + " form invalid");
            }
        };
    }]);

    module.controller('logoutCtrl', ["$http", function ($http) {

        this.logout = function () {
            $http({
                method: "GET",
                url: "/advertiser/logout"
            }).then(function (response) {
                if (response.data.success) {
                    document.location.reload();
                } else {
                    console.log(response.data.message);
                }
            }).catch(function (err) {
                console.log(err);
            });
        };
    }]);
}
